// common styles
@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,100,400italic,500,700,300italic&subset=latin,cyrillic);
@mixin f {
	font-family: Roboto, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

@include font(GothamPro-Medium, GothamPro-Medium);
@mixin fg {
	font-family: "GothamPro-Medium", Roboto, Arial, sans-serif; }

//colors
$blue: #00acd3;
$cl: #353535;
$border: #e0e0e0;
// channel colors
$orange: #d46b28;
$violet: #6d57a5;
$red: #b11a1b;
$green: #637824;

body {
	@include f;
	font-size: 14px;
	line-height: 1.3;
	color: $cl; }
button,
input,
textarea {
	@include f;
	font-size: 14px; }
button {
	background: none; }
a {
	text-decoration: none;
	color: $blue; }

.center {
	max-width: 1250px;
	margin: 0 auto;
	padding: 0 50px;
	@include r(1024) {
		padding: 0 30px; }
	@include r(768) {
		padding: 0 20px; }
	@include r(568) {
		padding: 0 15px; } }

//grid
.row {
	margin: 0 -15px;
	@include clr;
	[class*="col-"] {
		float: left;
		padding: 0 15px; } }
.col-20 {
	width: 20%; }
.col-25 {
	width: 25%; }
.col-33 {
	width: 33.33%; }
.col-50 {
	width: 50%; }
.col-75 {
	width: 75%; }


.icon {
	fill: currentColor; }

// channels
.channel {
	display: inline-block;
	vertical-align: middle;
	margin-right: 15px;
	font-size: 16px;
	color: #fff;
	&.channel_violet {
		color: $violet; }
	&.channel_orange {
		color: $orange; }
	&.channel_red {
		color: $red; } }

.btn {
	height: 55px;
	padding: 0 30px;
	background: #f8b636;
	border-radius: 5px;
	transition: background 200ms, color 200ms, border-color 200ms;
	color: #fff;
	font-size: 19px;
	border: 2px solid #f8b636;
	&:hover {
		background: darken(#f8b636, 5%); }
	&.btn_white {
		background: #fff;
		color: #979292;
		border: 2px solid #979292;
		&:hover {
			color: #b11226;
			border-color: #b11226; } }
	&.btn_small {
		height: 44px; }
	&.btn_blue {
		background: #44a3d8;
		border-color: #44a3d8;
		&:hover {
			background: darken(#44a3d8, 5%);
			border-color: darken(#44a3d8, 5%); } } }
a.btn {
	text-decoration: none;
	display: inline-block;
	line-height: 51px;
	&.btn_small {
		line-height: 40px; } }
.more {
	text-align: center;
	padding-top: 40px; }

select {
	width: 100%; }
.reset {
	color: #353535;
	transition: color 200ms;
	&:hover {
		color: #000; } }


// heading
.title {
	margin-bottom: 30px;
	@include fg;
	font-size: 36px;
	font-weight: 700;
	text-align: center;
	&.title_white {
		color: #fff; }
	@include r(1024) {
		font-size: 30px; }
	@include r(768) {
		font-size: 26px; } }
.headtitle {
	margin-bottom: 10px;
	color: #fff;
	font-size: 24px;
	font-weight: 300;
	text-transform: uppercase;
	strong {
		font-weight: 500; } }
.h1 {
	color: #fff;
	text-shadow: 0 1px 1px rgba(72, 54, 110, 0.75);
	font-size: 36px;
	font-weight: 900;
	@include r(768) {
		font-size: 32px; }
	@include r(480) {
		font-size: 30px; } }
.h2 {
	margin-bottom: 25px;
	color: #222121;
	font-size: 30px;
	font-weight: 300;
	text-shadow: 0 1px 1px rgba(72, 54, 110, 0.75);
	&.h2_white {
		color: #fff; }
	@include r(480) {
		padding: 0 10px; }
	@include r(414) {
		padding: 0; } }
.h3 {
	margin-bottom: 20px;
	font-size: 24px;
	font-weight: 300;
	color: #222121;
	@include r(480) {
		padding: 0 10px; }
	@include r(414) {
		padding: 0; } }
.h4 {
	font-size: 20px;
	margin-bottom: 20px;
	color: #353535;
	font-weight: 700;
	&.h4_white {
		color: #fff; } }

.scrolltop {
	display: none;
	position: fixed;
	top: 20px;
	left: 50%;
	margin-left: -103px;
	width: 206px;
	height: 48px;
	border-radius: 3px;
	background-color: #f8b636;
	padding: 0 10px 0 50px;
	font-weight: 700;
	color: rgba(#fff, 0.7);
	transform: translateY(-68px);
	opacity: 0;
	transition: transform 200ms, opacity 200ms;
	z-index: 99;
	&.visible {
		opacity: 1;
		transform: translateY(0); }
	.icon-top {
		position: absolute;
		top: 18px;
		left: 18px;
		font-size: 8px; }
	@include r(768) {
		display: block; } }
.feedback {
	font-size: 0;
	.feedback-link {
		display: block;
		height: 60px;
		padding: 20px 30px;
		background: #f5f5f5 url("../img/subscr.png") right bottom no-repeat;
		font-size: 16px;
		font-weight: 700;
		color: #353535; }
	.subscribe-link {
		display: block;
		height: 60px;
		padding: 20px 30px;
		background: #fff url("../img/feedback.png") right bottom no-repeat;
		font-size: 16px;
		font-weight: 700;
		color: #353535; } }
.share {
	img {
		display: block;
		max-width: 100%; } }

