		margin: 0 -10px 30px; }
	.film-main__item {
		position: relative;
		display: inline;
		vertical-align: middle;
		margin-left: 24px;
		&:before {
			content: "";
			position: absolute;
			top: -1px;
			left: -10px;
			width: 4px;
			height: 4px;
			border-radius: 50%;
			background: #fff; }
		&:first-child {
