//likes
.like-block {
	@include clr;
	padding: 14px 0 20px;
	.like, .dislike {
		width: 100px;
		height: 34px;
		border-radius: 3px;
		background: #e9e9e9;
		font-size: 18px;
		color: #717171;
		transition: background 200ms;
		&:hover {
			background: #d9d9d9; }
		span {
			font-size: 12px; }
		svg {
			position: relative;
			top: 2px; } }
	.like {
		float: left; }
	.dislike {
		float: right;
		svg {
			transform: rotate(180deg); } }
	@include r(568) {
		.like, .dislike {
			float: none;
			width: 100%;
			margin-bottom: 10px; } } }
// rating
.rating {
	margin-bottom: 20px;
	@include clr;
	.rating__title {
		@include r(600) {
			float: left;
			width: 50%; } }
	.rating__item {
		@include clr;
		@include r(600) {
			float: right;
			width: 50%;
			text-align: right; } }
	.rating__stars {
		float: left;
		font-size: 0; }
	.rating__value {
		float: right;
		font-size: 20px;
		font-weight: 900;
		color: #353535;
		@include r(600) {
			display: inline-block; } }
	@include r(1024) {
		.star {
			font-size: 17px; }
		.rating__value {
			font-size: 16px; } }
	@include r(900) {
		.star {
			font-size: 28px; }
		.rating__value {
			font-size: 20px; } }
	@include r(768) {
		.rating__stars {
			float: none;
			display: inline-block;
			vertical-align: middle; }
		.rating__value {
			float: none;
			display: inline-block;
			vertical-align: middle;
			text-align: center; }
		.star {
			font-size: 20px; } }
	@include r(600) {
		.rating__stars {
			display: inline-block;
			vertical-align: middle;
			width: auto;
			.star {
				display: none;
				font-size: 13px;
				&:first-child {
					display: inline-block;
					vertical-align: middle; } } }
		.rating__value {
			font-size: 14px; } }
	&.rating_small {
		.star {
			font-size: 17px; }
		.rating__value {
			font-size: 16px; }
		@include r(900) {
			.star {
				font-size: 28px; }
			.rating__value {
				font-size: 20px; } }
		@include r(768) {
			.rating__item {
				float: none;
				width: 100%; }
			.rating__stars {
				display: block;
				text-align: center;
				.star {
					font-size: 20px;
					display: inline-block; } }
			.rating__value {
				font-size: 30px;
				display: block;
				text-align: center; } } } }

.star {
